.catalog {
  padding-top: 32px;
  &__container {
    display: flex;
  }
  &__title {
    font-weight: 600;
    font-size: 28px;
    line-height: 35px;
    color: #3F3F3F;
    margin-bottom: 16px;
  }
}

.content {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  width: calc(1440px - 332px);
  &-list {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex-wrap: wrap;
    margin-left: 16px;
  }
  &-item {
    max-width: 332px;

    margin-bottom: 16px;

    &:not(:nth-child(3n)) {
      margin-right: 16px;
    }
  }
}

.custom-select {
  display: inline-flex;
  position: relative;
  transition: all 0.4s;
  margin-bottom: 16px;
  &__dropdown {
    display: none;
    position: absolute;
    left: 0;
    top: calc(100% + 2px);
    width: 100%;
    min-width: 115px;
    padding: 10px;
    z-index: 20;
    background-color: var(--color-white);
  }
  &__item {
    font-size: 12px;
    color: var(--color-grey);
    cursor: pointer;
    &:not(:last-child) {
      margin-bottom: 10px;
    }

    &:hover {
      color: var(--color-tundora);
      transition: color 0.3s;
    }
  }

  &__top {
    position: relative;
    max-width: 122px;
    padding: 10px 28px 10px 16px;
    font-size: 12px;
    line-height: 15px;
    color: var(--color-grey);
    background-color: #FFFEFB;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
    border-radius: 4px;
    cursor: pointer;

    &::after {
      content: "";
      position: absolute;
      right: 13px;
      top: 50%;
      transform: translateY(-50%);
      width: 10px;
      height: 6px;
      background-image: url(../../img/arrow-down.png);
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
    }
  }

  &--open {
    .custom-select__top {
      &:after {
        transform: translateY(-50%) rotate(180deg);
      }
    }

    .custom-select__dropdown {
      display: block;
    }
  }

  &--footer {
    margin-left: 14px;
    min-width: auto;
    .custom-select__top {
      padding-right: 13px;
      padding-bottom: 0;
      border-bottom: none;
      font-size: 13px;

      &::after {
        margin-top: 0;
      }
    }
  }
  &:hover, &:focus {
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
  }
}

@media (max-width: 1440px) {
  .content {
    &-item {
      &:not(:nth-child(2n)) {
        margin-right: 16px;
      }
    }
  }
}

@media (max-width: 1110px) {
  .catalog {
    &__container {
      flex-wrap: wrap;
      justify-content: center;
    }
    &-form {
      margin-bottom: 30px;
    }
  }
  .content {
    align-items: center;
    &-list {
      justify-content: center;
      margin-left: 0;
    }
  }
}

@media (max-width: 991px) {
  .product {
    &-content {
      &__delete {
        position: static !important;
        display: block !important;
      }
    }
  }
}

@media (max-width: 380px) {
  .catalog-form {
    .form{
      width: 300px;
    }
  }
  .content {
    &-item {
      &:not(:nth-child(2n)),&:not(:nth-child(3n)) {
        margin-right: 0;
      }
    }
  }
}
