.product {
  position: relative;
  background: #FFFEFB;
  box-shadow: 0px 20px 30px rgba(0, 0, 0, 0.04), 0px 6px 10px rgba(0, 0, 0, 0.02);
  border-radius: 4px;
  transition: all 0.4s;
  z-index: 1;
  &__img {
    max-height: 200px;
  }
  &-content {
    padding: 16px 16px 24px 16px;
    &__title {
      margin-bottom: 16px;
      a {
        font-weight: 600;
        font-size: 20px;
        line-height: 25px;
        color: var(--color-dark);
        transition: all 0.4s;
        &::before {
          content: '';
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          cursor: pointer;
        }

        &:hover {
          color: var(--main-color);
        }
        &:active {
          color: var(--main-color);
        }
        &:focus {
          outline: none;
          color: var(--main-color);
          border-bottom: 1px solid var(--main-color);
        }
      }
    }
    &__descr {
      margin-bottom: 32px;
      font-weight: 400;
      font-size: 16px;
      line-height: 20px;
      color: var(--color-dark);
    }
    &__price {
      font-weight: 600;
      font-size: 24px;
      line-height: 30px;
      color: var(--color-dark);
    }
    &__delete {
      position: absolute;
      top: -8px;
      right: -8px;
      display: none;
      align-items: center;
      justify-content: center;
      width: 32px;
      height: 32px;
      padding: 8px;
      background-color: var(--main-color);
      box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
      border-radius: 10px;
      transition: all 0.4s;
      z-index: 5;
      svg {
        width: 16px;
        height: 16px;
        fill: #fff;
        transition: all 0.4s;
      }

      &:active,
      &:focus {
        transition: all 0.4s;
        box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
        svg {
          fill: var(--color-dark);
        }
      }
      &:hover {
        transform: scale(1.08);
      }
      &:focus {
        outline: 1px solid var(--color-dark);
      }
    }
  }

  &:hover {
    .product-content__delete {
      display: inline-flex;
    }
  }
}
