.form {
  width: 332px;
  min-height: 440px;
  padding: 24px;
  background: #FFFEFB;
  box-shadow: 0px 20px 30px rgba(0, 0, 0, 0.04), 0px 6px 10px rgba(0, 0, 0, 0.02);
  border-radius: 4px;
  &-label {
      display: block;
      margin-bottom: 4px;
      font-size: 10px;
      line-height: 13px;
      letter-spacing: -0.02em;
      color: #49485E;
  }
  &-input,
  &-descr {
    width: 100%;
    font-size: 12px;
    line-height: 15px;
    color: var(--color-dark);
    border: none;
    border-radius: 4px;
    background: #FFFEFB;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
    transition: all 0.4s;
    &::placeholder {
      font-size: 12px;
      line-height: 15px;
      color: var(--color-grey);
    }
    &:hover {
      box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
    }
    &:active {
      background-color: #eee;
    }
    &:focus {
      outline: 1px solid var(--color-grey);
      box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
    }
  }
  &-group {
    position: relative;
    &:not(:last-of-type) {
      margin-bottom: 16px;
    }
    &:last-of-type {
      margin-bottom: 24px;
    }
  }
  &-input {
    height: 36px;
    padding: 0 16px;
    transition: all 0.4s;
  }
  &-descr {
    height: 108px;
    padding: 10px 16px;
    margin-bottom: 16px;
    resize: none;
  }
  &-error{
    display: none;
    position: absolute;
    bottom: -13px;
    left: 0;
    font-size: 8px;
    line-height: 10px;
    letter-spacing: -0.02em;
    color: #FF8484;
  }

  &-btn {
    width: 100%;
    height: 36px;
    font-weight: 600;
    font-size: 12px;
    line-height: 15px;
    text-align: center;
    letter-spacing: -0.02em;
    color: var(--color-grey);
    background-color: #EEEEEE;
    border-radius: 10px;

    &--active {
      color: #FFFFFF;
      background-color: #7BAE73;
      box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
    }

    &:hover {
      box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
    }
    &:active {
      background-color: #eee;
      color: #49485E;
    }
    &:focus {
      outline: 1px solid var(--color-dark);
      box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
    }
  }
}

.input-price {
  margin-bottom: 24px !important;
}


.label-name,
.label-link,
.label-price{
  span{
    position: relative;
    &::after {
      content: '';
      position: absolute;
      top: 0;
      right: -5px;
      display: block;
      width: 4px;
      height: 4px;
      border-radius: 100%;
      background-color: #FF8484;
    }
  }
}


.just-validate-error-label {
  margin-top: 4px;
  font-size: 8px;
  line-height: 10px;
  letter-spacing: -0.02em;
}
