// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: 'Source Sans Pro', sans-serif;
  --content-width: 1376px;
  --container-offset: 32px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --color-white: #fff;
  --color-dark: #3f3f3f;
  --color-grey: #b4b4b4;
  --main-color: #ff8484;
}
